.dialog {
    flex-grow: 1;
    position: relative;
  }
  
  .overflow {
    padding: 20px 20px 30px;
    position: absolute;
    width: 100%;
    max-height: 100%;
    left: 0;
    bottom: 0;
    overflow: auto;
    scrollbar-width: none;
  }
  
  .overflow::-webkit-scrollbar {
    display: none;
  }